.more-info {
    background-color: $secondary;

    &__container {
        @extend %flex;
        flex-direction: column;
        justify-content: space-between;
        padding: {
            top: 74px;
            bottom: 100px; }

        @media (max-width: 1500px) {
            row-gap: 80px; } }

    &__top-content {
        width: 100%;
        display: flex;
        justify-content: space-between;

        @media (max-width: 1500px) {
            flex-direction: column;
            align-items: center;
            row-gap: 80px; } }

    //Top content


    //Headshots

    &__headshots {
        width: 614px;
        margin-right: $noclip*2; }

    &__headshots-header {
        margin-bottom: 34px; }

    &__headshots-caption {
        line-height: 32px;
        position: relative; }

    &__arrow {
        position: absolute;
        margin: {
            top: 35px;
            left: 9px; }

        @media (max-width: 1500px) {
            display: none; } }


    //Tips

    &__tips {
        width: 716px;

        &-header {
            margin-bottom: 62px;

            @media (max-width: 1500px) {
                text-align: center; } } }



    &__tip {
        @extend %flex;

        @media (max-width: 767px) {
            justify-content: center; }

        &-list {
            font-size: 16px;
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-areas: "A B" "C D" "E F";
            column-gap: 110px;
            row-gap: 60px;
            margin-left: 10px;

            @media (max-width: 1500px) {
                margin-left: unset; }

            @media (max-width: 767px) {
                grid-template-columns: 1fr;
                grid-template-areas: "A" "B" "C" "D" "E" "F"; } }

        img {
            max-height: 61px;
            max-width: 76px;
            margin-right: 25px; }

        &_move-right {
            grid-area: F;
            @media (max-width: 767px) {
                grid-area: unset; } }

        &-caption {
            max-width: 230px;
            line-height: 24px; }

        @media (max-width: 1024px) {
            font-size: $main_font-size - 2px; }

        @media (max-width: 425px) {
            font-size: $main_font-size - 4px; } }


    &__headshots, &__tips {
        @media (max-width: 1500px) {
            width: 60%;
            margin-right: 0; }

        @media (max-width: 920px) {
            width: 80%; }

        @media (max-width: 520px) {
            width: 90%; }

        @media (max-width: 375px) {
            width: 100%; } }


    //Bottom content

    &__bottom-content {
        margin-right: 240px;

        @media (max-width: 1500px) {
            margin-right: 0;
            width: 80%; }

        @media (max-width: 768px) {
            width: 90%; }

        @media (max-width: 640px) {
            width: 100%; } }

    &__headshot-services {
        width: 1060px;
        margin-top: 15px;
        padding: 60px 80px;
        box-sizing: border-box;
        @extend %dark-block;

        @media (max-width: 1500px) {
            width: 100%; }

        @media (max-width: 640px) {
            padding: 60px 40px; }

        @media (max-width: 375px) {
            padding: 30px 20px; } }

    &__bottom-title, &__bottom-paragraph:nth-child(2) {
        margin-bottom: 46px; }

    &__bottom-title {
        font-size: 22px;
        text-transform: uppercase;

        @media (max-width: 425px) {
            text-align: center; } }

    &__bottom-paragraph {
        line-height: 32px; } }
