.blog {

    &__container {
        @extend %flex;
        flex-direction: column; }

    &__header {
        margin-top: 90px;

        &-caption {
            margin-top: 14px; } }

    &__entries {
        margin-top: 80px;
        gap: 20px;
        flex-wrap: wrap;
        justify-content: center; }

    &__entry {
        width: 386px;
        background-color: $secondary;

        img {
            width: 100%; }

        @media (max-width: 768px) {
            width: 45%; }

        @media (max-width: 660px) {
            width: 80%; }


        &-text {
            padding: 29px 21px;
            min-height: 200px;
            @extend %flex;
            justify-content: space-between;
            flex-direction: column;

            @media (max-width: 660px) {
                min-height: unset; }

            @media (max-width: 320px) {
                padding: 16px 10px; } }


        &-title {
            font: {
                size: 20px;
                weight: 600; }
            text-transform: uppercase;
            line-height: 28px;

            @media (max-width: 768px) {
                font-size: 18px; }

            @media (max-width: 660px) {
                margin-bottom: 20px; }

            @media (max-width: 374px) {
                font-size: 16px; } }


        &-bottom-data {
            width: 100%;
            @extend %flex;
            justify-content: space-between;

            @media (max-width: 374px) {
                flex-direction: column;
                row-gap: 10px; } }


        &-comments {
            @extend %flex;
            gap: 9px;
            color: $accent_bright;

            img {
                width: unset; } } }

    &__btn {
        padding: {
            left: 53px;
            right: 53px; }
        margin: {
            top: 60px;
            bottom: 120px; } }

    &__link {
        display: block;
        width: 100%;
        height: 100%; } }
