.footer {
    @extend %dark-block;
    padding: {
        top: 90px;
        bottom: 27px; }

    @media (max-width: 1024px) {
        padding-top: 70px; }


    &__container {
        @media (max-width: 1440px) {
            max-width: 1200px; }

        @media (max-width: 1024px) {
            max-width: 920px; } }

    &__main-content {
        font-size: 16px;
        display: grid;
        grid-template-columns: repeat(2, 1fr) auto 1fr;
        grid-template-rows: 1fr auto;
        column-gap: 13px;
        row-gap: 75px;
        margin-bottom: 84px;

        @media (max-width: 1440px) {
            grid-template-columns: repeat(3, 1fr);
            row-gap: 40px;
            margin-bottom: 50px; }

        @media (max-width: 640px) {
            grid-template-columns: repeat(2, 1fr); }

        @media (max-width: 374px) {
            font-size: 14px; } }

    &__add-content {
        font-family: MrEavesXLModOT-Book;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: 1fr;
        opacity: 0.2; }


    //Grid markup

    &__company-logo {
        @media (max-width: 1024px) {
            display: none; } }

    &__resources {
        @media (max-width: 1440px) {
            grid-area: 1 / 3 / 2 / 5; }

        @media (max-width: 1024px) {
            grid-area: 1 / 2 / 2 / 5; }

        @media (max-width: 640px) {
            display: none; } }

    &__address {
        grid-area: 2 / 1 / 3 / 2; }

    &__contact {
        grid-area: 2 / 2 / 3 / 3;

        @media (max-width: 1024px) {
            grid-area: 2 / 2 / 3 / 4; } }

    &__sm {
        grid-area: 2 / 4 / 3 / 5; }

    &__address, &__contact, &__sm {
        @media (max-width: 640px) {
            grid-area: unset; } }


    //Section content styles

    &__section-title {
        font: {
            size: 21px;
            weight: 400; }
        line-height: 32px;
        margin-bottom: 28px;
        color: $footer-list-title; }

    &__resources {
        list-style-image: url('arrow_11880.png'); }

    &__sm-list {
        text-align: right; }

    &__links-list, &__sm-list, &__resources-list, &__address {
        display: block;
        line-height: 32px; }

    &__contact-link {
        display: block;
        font-size: 30px;

        @media (max-width: 1440px) {
            font-size: 26px; }

        @media (max-width: 1024px) {
            font-size: 22px; }

        @media (max-width: 640px) {
            font-size: 18px; } }

    &__copyright {
        grid-area: 1 / 2 / 2 / 3;
        justify-self: center; }

    &__policy {
        grid-area: 1 / 3 / 2 / 4;
        justify-self: right; } }
