.header {
    white-space: nowrap;
    font-size: 16px;
    background-color: $primary;

    @media (max-width: 1100px) {
        position: fixed;
        width: 100%;
        z-index: 2;
        box-shadow: 0px 2px 8px 3px rgba(50, 50, 50, 0.3); }

    &__mobile-menu {
        display: none;
        background: none;
        border: none;
        cursor: pointer;
        font-weight: 700;
        font-size: 18px;

        @media (max-width: 1100px) {
            display: block; } }

    &__burger-menu {
        width: 40px;
        height: 40px;

        img {
            width: 100%; } }

    &__container {
        max-width: 1780px;
        height: 100px;
        padding: 0 $noclip;
        @extend %flex;
        justify-content: space-between;

        @media (max-width: 1100px) {
            height: 60px; }

        @media (max-width: 768px) {
            padding: 0 4vw; } }

    &__title {
        padding-top: 15px;

        @media (max-width: 1100px) {
            flex-grow: 1;
            text-align: center;
            font-size: 40px;
            padding-top: 10px; }

        @media (max-width: 768px) {
            display: none; } }


    &__section-list {
        column-gap: 38px;
        margin: {
            left: $noclip;
            right: $noclip*2; }
        font-weight: 500; }

    &__sm-list {
        column-gap: 20px;
        margin-left: $noclip; }

    &__section-list, &__sm-list {

        @media (max-width: 1100px) {
            position: absolute;
            background-color: $primary;
            top: 60px;
            left: 0;
            width: 100%;
            margin: unset;
            flex-direction: column;
            align-items: center;
            transition: $transition-default; }

        li {
            @media (max-width: 1100px) {
                width: 100%;
                text-align: center;
                padding: {
                    top: 12px;
                    bottom: 12px; }
                border-top: 1px solid $accent; } } }

    &__sm-link {
        @extend %flex;
        @media (max-width: 1100px) {
            width: fit-content;
            margin: auto;
            min-width: 120px; }

        span {
            order: 1;
            margin-left: 20px;
            @media (min-width: 1100px) {
                display: none; } } }

    &__btn {
        box-sizing: border-box;
        padding: 11px 22px 11px 22px; }

    &__left-content, &__right-content {
        @extend %flex;
        justify-content: space-between; }

    &__links {
        @media (max-width: 375px) {
            display: none; } }

    &__left-content {
        width: 838px; }

    &__right-content {
        width: 344px;
        @media (max-width: 768px) {
            flex-grow: 1;
            justify-content: flex-end;
            gap: 40px; } }

    &__left-content, &__right-content {
        @media (max-width: 768px) {
            width: unset; } } }
