.banner {
    color: $primary;
    position: relative;
    height: 510px;
    overflow: hidden;
    background-color: $image-holder;

    &__img {
        min-width: 100%;
        z-index: 0;
        position: absolute;
        top: 0;
        left: 0;
        filter: brightness(0.7);

        @media (max-width: 1440px) {
            transform: translateX(-10%); }

        @media (max-width: 1024px) {
            transform: translateX(-15%); }

        @media (max-width: 768px) {
            transform: translateX(-30%); }

        @media (max-width: 425px) {
            transform: translateX(-38.5%); } }


    &__container {
        position: relative;
        z-index: 1;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;

        @media (max-width: 1440px) {
            max-width: 1200px; }

        @media (max-width: 1024px) {
            max-width: 920px; }

        @media (max-width: 768px) {
            align-items: center; } }


    &__paragraph-big {
        font-size: 70px;
        font-weight: 700;

        @media (max-width: 1440px) {
            font-size: 55px; }

        @media (max-width: 1024px) {
            font-size: 50px; }

        @media (max-width: 375px) {
            font-size: 40px; } }


    &__paragraph-small {
        font-size: 21px;
        margin: {
            top: 38px;
            bottom: 46px; }

        @media (max-width: 1024px) {
            font-size: 18px; }

        @media (max-width: 375px) {
            font-size: 16px; }

        @media (max-width: 375px) {
            text-align: center; } }


    &__btn {
        padding: {
            left: 35px;
            right: 35px; } } }
